.projectsContainer {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}
.ModalItem {
  width: 80vw;
  height: 80vw;
  margin: 10px 10px;
  background-color: rgba(75, 75, 75, 0);
  border: none;
  padding: 0px;
}
img {
  display: block;
  margin: auto;
  z-index: 0;
  width: 80vw;
  height: auto;
  border-radius: 25px;
  box-shadow: 0px 0px 5px 3px rgb(39, 50, 68);
}

.button-content {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  &:before {
    content: attr(data-content);
    position: absolute;
    color: rgb(250, 250, 250);
    font-size: 4vh;
    font-weight: 600;
    width: 90%;
    top: 20%;
    left: 50%;
    z-index: 2;
    opacity: 1;
    transform: translate(-50%, -50%);
  }
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: calc(100% + 1px);
    top: 0;
    left: 0;
    z-index: 1;
    opacity: 1;
    background: #00000083;
    border-radius: 25px;
  }
}
.ModalItem:focus,
.button-content {
  outline: none;
  position: relative;
}
@media (min-width: 900px) {
  .projectsContainer {
    width: 80vw;
  }
  .ModalItem {
    width: 45%;
    max-width: 515px;
    height: auto;
    .button-content {
      &:after {
        opacity: 0.3;
        background: #0000008e;
      }
      &:before {
        opacity: 0;
      }
      img {
        width: 100%;
      }
    }
  }

  :hover > .button-content {
    cursor: pointer;
    img {
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
    &:before {
      top: 80%;
      opacity: 1;
      transition: all 400ms ease-in-out;
    }
    &:after {
      border: 1px solid #fff;
      opacity: 1;
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
  }
  :active > .button-content {
    cursor: pointer;
    img {
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
    &:before {
      top: 80%;
      opacity: 1;
      transition: all 400ms ease-in-out;
    }
    &:after {
      border: 1px solid #fff;
      opacity: 1;
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
  }
  button:not(:-moz-focusring):focus > .ComplexButton-Content {
    box-shadow: none;
  }

  :focus > .button-content {
    cursor: pointer;
    img {
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
    &:before {
      top: 80%;
      opacity: 1;
      transition: all 400ms ease-in-out;
    }
    &:after {
      border: 1px solid #fff;
      opacity: 1;
      transform: scale(1.05);
      transition: all 400ms ease-in-out;
    }
  }
}
@media (min-width: 1360px) {
  .ModalItem {
    width: 45%;
    max-width: 515px;
    height: auto;
    margin: 10px 5%;
  }
  .projectsContainer {
    flex-direction: row;
    width: 100%;
  }
}
